html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Oswald", sans-serif;
  line-height: 1.3;
}

h1, h2, h3 {
  margin: 0;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
}

h2, h3 {
  font-weight: 400;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

a {
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  display: block;
  color: #fff;
}

p {
  font-family: "Montserrat", sans-serif;
  line-height: 1.6;
}

img {
  width: 100%;
  margin: 0 auto;
  height: auto;
  display: block;
}

section {
  padding: 5rem 0;
}

ul {
  list-style: none;
  padding: 0;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
  width: 90%;
}

.negrita {
  font-weight: bold;
  font-style: italic;
}

.subrayado {
  text-decoration: underline;
}

.hamburger {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
  transition: transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
  z-index: 1002;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}
.hamburger.is-active {
  background-color: none;
}

.layer {
  background-color: #fff;
  margin-bottom: 4px;
  border-radius: 2px;
  width: 29px;
  height: 4px;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: all 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
  transition: all 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
}

.layer.negro {
  background-color: #000;
}

.hamburger:hover .top {
  -webkit-transform: translateY(-100%);
  -ms-transform: translateY(-100%);
  transform: translateY(-100%);
}

.hamburger:hover .bottom {
  -webkit-transform: translateY(50%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
}

.hamburger.is-active .top {
  -webkit-transform: translateY(200%) rotate(45deg);
  -ms-transform: translateY(200%) rotate(45deg);
  transform: translateY(200%) rotate(45deg);
  background-color: #000;
}

.hamburger.is-active .mid {
  opacity: 0;
}

.hamburger.is-active .bottom {
  -webkit-transform: translateY(-200%) rotate(135deg);
  -ms-transform: translateY(-200%) rotate(135deg);
  transform: translateY(-200%) rotate(135deg);
  background-color: #000;
}

.header {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%), url("../img/hero-main.jpg");
  background-position: center center, center center;
  background-repeat: no-repeat;
  background-size: 100%, cover;
  color: #fff;
}
.header__top {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  height: 6rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  transition: background-color 0.3s;
}
@media (min-width: 768px) {
  .header__top {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header__enlace {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.header__imagen {
  width: 4rem;
}
.header__texto-logo {
  font-size: 2.1rem;
  font-weight: 300;
  font-family: "Oswald", sans-serif;
  display: none;
}
@media (min-width: 768px) {
  .header__texto-logo {
    display: block;
  }
}
.header__texto-logo span {
  text-transform: uppercase;
}

.bg-blanco {
  background-color: #fff;
  -webkit-box-shadow: 0px 1px 3px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 1px 3px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 1px 3px -1px rgba(0, 0, 0, 0.75);
}

.text-black {
  color: #000;
}

.navegacion {
  position: fixed;
  top: 0;
  left: 0;
  right: 10rem;
  bottom: 0;
  background-color: #fff;
  padding: 8rem 0 0 5rem;
  transform: translateX(-35rem);
  transition: transform 0.5s;
}
.navegacion__flex {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.navegacion__enlace {
  color: #000;
  font-family: "Montserrat", sans-serif;
  display: inline-block;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    color: #fff;
  }
}
.navegacion__enlace--activo {
  background-color: #000;
  color: #fff;
  font-weight: 700;
}
.navegacion__enlace:hover {
  color: #e1e1e1;
}
.navegacion__mostrar {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .navegacion {
    position: unset;
    background-color: transparent;
    display: flex;
    padding: 0;
    transform: translateX(0);
    transition: none;
  }
  .navegacion__flex {
    flex-direction: row;
    align-items: center;
  }
}

